}

.six .modal-background {
  background: none;
  animation: .5s cubic-bezier(.165, .84, .44, 1) forwards fadeIn;
}

.six .modal-background .modal {
  background-color: #0000;
  animation: .5s cubic-bezier(.165, .84, .44, 1) .8s forwards modalFadeIn;
}

.six .modal-background .modal h2, .six .modal-background .modal p {
  opacity: 0;
  animation: .5s cubic-bezier(.165, .84, .44, 1) 1s forwards modalContentFadeIn;
  position: relative;
}

.six .modal-background .modal .modal-svg rect {
  animation: .5s cubic-bezier(.165, .84, .44, 1) .3s forwards sketchIn;
}

.six.out {
  animation: 0s linear .5s forwards quickScaleDown;
}

.six.out .modal-background {
  animation: .5s cubic-bezier(.165, .84, .44, 1) forwards fadeOut;
}

.six.out .modal-background .modal {
  animation: .5s cubic-bezier(.165, .84, .44, 1) forwards modalFadeOut;
}

.six.out .modal-background .modal h2, .six.out .modal-background .modal p {
  animation: .5s cubic-bezier(.165, .84, .44, 1) forwards modalContentFadeOut;
}

.six.out .modal-background .modal .modal-svg rect {
  animation: .5s cubic-bezier(.165, .84, .44, 1) forwards sketchOut;
}

@keyframes unfoldIn {
  0% {
    transform: scaleY(.005)scaleX(0);
  }

  50% {
    transform: scaleY(.005)scaleX(1);
  }

  100% {
    transform: scaleY(1)scaleX(1);
  }
}

@keyframes unfoldOut {
  0% {
    transform: scaleY(1)scaleX(1);
  }

  50% {
    transform: scaleY(.005)scaleX(1);
  }

  100% {
    transform: scaleY(.005)scaleX(0);
  }
}

@keyframes zoomIn {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes zoomOut {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

@keyframes fadeIn {
  0% {
    background: none;
  }

  100% {
    background: #000000b3;
  }
}

@keyframes fadeOut {
  0% {
    background: #000000b3;
  }

  100% {
    background: none;
  }
}

@keyframes scaleUp {
  0% {
    opacity: 0;
    transform: scale(.8)translateY(1000px);
  }

  100% {
    opacity: 1;
    transform: scale(1)translateY(0);
  }
}

@keyframes scaleDown {
  0% {
    opacity: 1;
    transform: scale(1)translateY(0);
  }

  100% {
    opacity: 0;
    transform: scale(.8)translateY(1000px);
  }
}

@keyframes scaleBack {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(.85);
  }
}

@keyframes scaleForward {
  0% {
    transform: scale(.85);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes quickScaleDown {
  0% {
    transform: scale(1);
  }

  99.9% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

@keyframes slideUpLarge {
  0% {
    transform: translateY(0%);
  }

  100% {
    transform: translateY(-100%);
  }
}

@keyframes slideDownLarge {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(0%);
  }
}

@keyframes moveUp {
  0% {
    transform: translateY(150px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes moveDown {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(150px);
  }
}

@keyframes blowUpContent {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  99.9% {
    opacity: 0;
